import "core-js/modules/es.array.push.js";
import { reactive, ref, toRefs } from '@vue/reactivity';
import { getData } from "../assets/api/home";
import { useRoute, useRouter } from 'vue-router';
import { getvideoData } from "../assets/api/video";
export default {
  setup() {
    let router = useRouter();
    // 为你推荐数据
    let route = useRoute();
    // 接受的id值
    let id = ref(route.query.id);
    let banner = reactive({
      // 轮播图
      bannerList: null,
      // 轮播图下面的tab
      menuList: null,
      // tab下面的动图
      tabImg: null,
      // 精选课程
      hotClass: null,
      video: ""
    });
    //返回
    let back = () => {
      router.push({
        path: "/video"
      });
    };
    let getDataFun = () => {
      getData().then(data => {
        // 轮播图
        banner.bannerList = data.data.results.body.sybnList;
        // 轮播图下面的tab
        banner.menuList = data.data.results.body.menuList;
        // 精选课程
        banner.hotClass = data.data.results.body.netList;
        // tab下面的动图
        banner.tabImg = data.data.results.body.syzwList;
      });
      getvideoData().then(res => {
        // console.log(res.data.data.response.videos);
        // banner.video = res.data.data.response.videos[13].play_url
        banner.video = res.data.data.response.videos[id.value];
        console.log(banner.video);
      });
      // console.log(id.value,1111);
    };

    getDataFun();

    //界面跳转
    let navigate = id => {
      console.log(id);
      router.push({
        path: "/netshop",
        query: {
          key: id
        }
      });
      // console.log(router);
    };
    //返回
    let goback = () => {
      router.go(-1);
    };
    let player = ref(false);
    let pause = () => {
      console.log("暂停");
      player.value = false;
    };
    let play = () => {
      console.log("播放");
      player.value = true;
    };
    return {
      ...toRefs(banner),
      navigate,
      back,
      goback,
      id,
      player,
      pause,
      play
    };
  }
};